import { useNavigate } from "react-router-dom";
import { useContext, useEffect, useState } from "react";

import { ShootingTestContext } from "../../context/ShootingTestContext";

import Button from "../../components/Button";

import xIcon from "../../assets/icons/x.svg";
import arrowIcon from "../../assets/icons/arrow.svg";

import "./styles.css";

export default function Questions() {
  const {
    closeUI,
    questions,
    setUserAnswers,
  } = useContext(ShootingTestContext);

  const [ currentQuestionIndex, setCurrentQuestionIndex ] = useState(0);

  const [ selectedAnswer, setSelectedAnswer ] = useState(null);

  const navigate = useNavigate();

  useEffect(() => {
    setUserAnswers([]);
  }, []);

  useEffect(() => {
    function handleKeyDown(e) {
      if(e.key === "Escape") {
        closeUI();
      };
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  return (
    <main 
      id="shootingTestQuestions"
      className="container"
    >
      <Button 
        icon={xIcon}
        type="closeBtn"
        onClick={closeUI}
      />

      <section>
        <h1>Questo {currentQuestionIndex + 1} de {questions.length}</h1>

        <p> 
          { questions[currentQuestionIndex].question }
          </p>

        <div className="answersContainer">
          { questions[currentQuestionIndex].answers.map( (answer, index) => {
            const selected = index === selectedAnswer;
            return (
              <button
                key={index}
                className={`answerBtn ${ selected ? "active" : ""}`}
                onClick={() => selected ? setSelectedAnswer(null) : setSelectedAnswer(index)}
              >
                { answer }
              </button>
            )
          })}
        </div>

        <Button 
          icon={arrowIcon}
          label={currentQuestionIndex + 1 < questions.length ? "Prxima pergunta" : 'Finalizar teste'}
          onClick={() => {
            if( selectedAnswer === null ) return;

            const isLastQuestion = currentQuestionIndex + 1 === questions.length;

            setUserAnswers( prevAnswer => [...prevAnswer, selectedAnswer]);

            if( isLastQuestion ) navigate("/result");

            setCurrentQuestionIndex( prevIndex => ++prevIndex );
            setSelectedAnswer(null);
          }}
        />
      </section>
    </main>
  )
};