import React, { createContext, useContext, useEffect, useState } from "react";
import { fetchNui } from "../utils/fetchNui";
import { isEnvBrowser } from "../utils/misc";
import { useNuiEvent } from "../hooks/useNuiEvent";
import RoutesManager from "../routes";

export const VisibilityCtx = createContext(null);

// This should be mounted at the top level of your application, it is currently set to
// apply a CSS visibility value. If this is non-performant, this should be customized.

/**
 * 
 * @param {React DOM} param0 Neste caso, children tem o valor do componente App
 * @returns a renderizacao do children, de acordo com o valor da visibilidade (param -> visible)
 */
export const VisibilityProvider = () => {
  const [visible, setVisible] = useState(false);
  const [redirectParams, setRedirectParams] = useState({})
  const [requestedRoute, setRequestedRoute] = useState('')

  useNuiEvent('redirect', (data) => {
    setVisible(!visible)
    window.location.hash = data.route
    setRequestedRoute(data.route)
    setRedirectParams(data.params)
  })

  useNuiEvent('closeInterface', (data) => {
    setVisible(false)
  })

  // Handle pressing escape/backspace
  useEffect(() => {
    if (isEnvBrowser()) {
      setVisible(true)
    }
    // Only attach listener when we are visible
    if (!visible) {
      return
    };
  }, [visible]);

  return (
    <VisibilityCtx.Provider
      value={{
        visible,
        setVisible,
        redirectParams
      }}
    >

      <div style={{ display: visible ? 'block' : 'none', height: '100%' }}>
        <RoutesManager />
      </div>
    </VisibilityCtx.Provider>
  );
};

export const useVisibility = () => useContext(VisibilityCtx);
