import { useContext, useEffect, useState } from "react";

import { ShootingTestContext } from "../../context/ShootingTestContext";

import targetIcon from "../../assets/icons/target.svg";

import "./styles.css";
import { useNuiEvent } from "../../../../hooks/useNuiEvent";

export default function HotTrack() {
  const {
    closeUI,
    beepAudio,
    targetsHit,
    targetsAmount,
    setTargetsHit,
  } = useContext(ShootingTestContext);

  const [ testIsRunning, setTestIsRunning ] = useState(false);

  useNuiEvent('handleTest',(data) => {
    setTargetsHit([])
    setTestIsRunning(data)
  })
  
  const [ counterIsRunning, setCounterIsRunning ] = useState(false);
  const [ counter, setCounter ] = useState(3);

  useEffect(() => {
    function handleKeyDown(e) {
      if(e.key.toUpperCase() === "E" && !testIsRunning && !counterIsRunning) {
        setCounterIsRunning(true);
        beepAudio.play();
        
        const timerId = setInterval(() => {
          setCounter(prevTime => {
            if (prevTime > 0) {
              beepAudio.play();
              return --prevTime;
            };

            clearInterval(timerId);
            setTestIsRunning(true);
            setCounterIsRunning(false);
            return prevTime;
          });
        }, 1000);
      };

      if(e.key.toUpperCase() === "N" && !testIsRunning && !counterIsRunning) {
        closeUI();
      };
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [testIsRunning, counterIsRunning]);

  return (
    <main
      id="shootingTestHotTrack"
      className={`${testIsRunning ? "testRunning" : ""} ${counterIsRunning ? "counterRunning" : ""}`}
    >
      <section>
        <h1 onClick={() => setTargetsHit(prevHits => [...prevHits, Math.random() > 0.5 ? true : false])}>Pista quente</h1>

        <p>Procure acertar os alvos com preciso</p>

        <article className="targetsGrid">
          {Array.from({ length: targetsAmount }).map( (_, index) => (
            <div 
              key={index}
              className={`targetItem ${targetsHit[index] ? "targetHit" : ""}`}
            >
              <img src={targetIcon} />
            </div>
          ))}
        </article>
      </section>

      <aside>
        <div>
          <p className="styledLetter">
            E
          </p>
          <p>Pressione a <strong>E</strong> para comear o teste de tiros</p>
        </div>

        <div>
          <p className="styledLetter">
            N
          </p>
          <p>Pressione a <strong>N</strong> para cancelar o teste de tiros</p>
        </div>
      </aside>

      { counterIsRunning && (
        <div
          id="counter"
          key={counter}
        >
          { counter }
        </div>
      )}
    </main>
  )
};