import { createContext, useEffect, useState } from "react";

import beepSound from "../assets/beep.mp3";
import { fetchNui } from "../../../utils/fetchNui";
import { useNuiEvent } from "../../../hooks/useNuiEvent";

export const ShootingTestContext = createContext();

export default function ShootingTestContextProvider({ children }) {
  function closeUI() {
    fetchNui('hideFrame')
  };

  const [ testPrice, setTestPrice ] = useState(50000);

  const [ questions, setQuestions ] = useState([
    {
      question: "Qual armamento o senhor(a) pode utilizar em questo de um assalto que voc viu no meio da rua da cidade ?",
      answers: [
        "Resposta A",
        "Resposta A",
        "Resposta A",
        "Resposta A",
      ],
      rightAnswer: 0, // index
    },
  ]);

  const [ userAnswers, setUserAnswers  ] = useState([]);

  const [ validity, setValidity ] = useState( null ); // null if it does not exist

  const [ targetsAmount, setTargetsAmount ] = useState(10);

  const [ targetsHit, setTargetsHit ] = useState([]);
  // Adicione um fetch que incrementa o targetsHit quando acertar o alvo

  useNuiEvent('setTargetsHit',(hit) => {
    setTargetsHit(prevHits => [...prevHits, hit]);
  });

  useEffect(()=>{
  }, [targetsHit])
  
  const beepAudio = new Audio(beepSound);
  beepAudio.volume = .3;

  async function searchUser(inputValue) {
    let userData = await fetchNui('searchUserLicenseData', inputValue)
    console.log('userData', JSON.stringify(userData))
    if(userData){
      userData.validity = new Date(userData.validity)
      return userData
    }

    return null
  };

  function seekGunLicense(user) {
    if(user.state == 'Ativa' || user.state == 'Expirada'){
      fetchNui('revokeGunLicense', user)
    }
  };

  useNuiEvent('setData', (data) => {
    setQuestions(data.TestQuestions)
    setTestPrice(data.TestPrice)
  })
  
  return (
    <ShootingTestContext.Provider value={{
      closeUI,
      validity,
      testPrice,
      questions,
      beepAudio,
      searchUser,
      seekGunLicense,

      userAnswers,
      setUserAnswers,

      targetsHit,
      targetsAmount,
      setTargetsHit
    }}>
      { children }
    </ShootingTestContext.Provider>
  )
}