import "./styles.css";

export default function Button({ label, icon, type, ...rest }) {
  return (
    <button 
      className={`btn ${icon ? "btn_icon" : ""} ${type ? type : ""}`}
      {...rest}
    >
      { label }
      { icon && (
        <img src={icon} alt="" />
      )}
    </button>
  )
};