import { useContext, useEffect, useState } from "react";

import { ShootingTestContext } from "../../context/ShootingTestContext";

import Button from "../../components/Button";

import searchIcon from "../../assets/icons/search.svg";
import personIcon from "../../assets/icons/person.svg";

import "./styles.css";
import { fetchNui } from "../../../../utils/fetchNui";

export default function SeekGunLicense() {
  const {
    closeUI,
    searchUser,
    seekGunLicense,
  } = useContext(ShootingTestContext);

  const [ inputValue, setInputValue ] = useState();

  const [ searchedUser, setSearchedUser ] = useState(undefined);

  useEffect(() => {
    function handleKeyDown(e) {
      if(e.key === "Escape") {
        closeUI();
      };
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  return (
    <main
      id="shootingTestSeekLicense"
    >
      <div className="seekLicenseInputContainer">
        <label htmlFor="seekLicenseInput">
          <img src={searchIcon} />
        </label>

        <input 
          id="seekLicenseInput"
          placeholder="Pesquisar passaporte"
          value={inputValue}
          onChange={ e => setInputValue(e.target.value) }
        />

        <Button
          type="small"
          label="Buscar"
          onClick={ async () => {
            const user = await searchUser(inputValue);
            setSearchedUser(user);
          }}
        />
      </div>

      <section>
        { searchedUser === undefined ? (
          <p>Digite no campo para fazer a busca pelo usurio</p>
        ) : (searchedUser === null) ? (
          <p><strong>Infelizmente</strong>, no encontramos nenhum usurio com porte de arma com este passaporte.</p>
        ) : (
          <div className="searchedUser">
            <div className="imgContainer">
              <img src={personIcon} />
            </div>

            <aside>
              <p>{searchedUser.name}</p>
              <p>{searchedUser.state}</p>
              <p>{searchedUser.validity.toLocaleDateString("pt-BR")}</p>
            </aside>
          </div>
        )} 
      </section>

      { searchedUser !== undefined && (
        <Button
          type="medium"
          label={(searchedUser === null || searchUser === undefined ? "Fechar" : 
            searchedUser.state === 'Ativa' ? "Caar porte" : 
            searchedUser.state === 'Cassada' ? 'Ativar Porte' : 'Fechar'
          )}
          
          onClick={() => {
            if(searchedUser === null) return closeUI();            
            
            if(searchedUser.state === 'Ativa'){
              seekGunLicense(searchedUser);
              setSearchedUser((prev) => ({
                ...prev,
                state: 'Cassada'
              }))
            }

            if(searchedUser.state === 'Cassada' && searchedUser.state !== 'Expirada'){
              fetchNui('grantGunLicense', searchedUser)
              setSearchedUser((prev) => ({
                ...prev,
                state: 'Ativa'
              }))
            }
          }}
        />
      )}
    </main>
  )
};