import { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import { ShootingTestContext } from "../../context/ShootingTestContext";

import Button from "../../components/Button";

import xIcon from "../../assets/icons/x.svg";

import "./styles.css";
import { fetchNui } from "../../../../utils/fetchNui";

export default function Result() {
  const {
    closeUI,
    questions,
    userAnswers,
  } = useContext(ShootingTestContext);

  const navigate = useNavigate();

  function getTestResult() {
    let testResult = {
      rightAnswers: 0,
      wrongAnswers: 0,
    };

    for (let i = 0; i < questions.length; i++) {
      userAnswers[i] === questions[i].rightAnswer ? testResult.rightAnswers++ : testResult.wrongAnswers++;
    };

    return {
      ...testResult,
      percentage: Math.round(testResult.rightAnswers / questions.length * 100),
    };
  };

  const { rightAnswers, wrongAnswers, percentage } = getTestResult();

  const resultFields = [
    {
      label: "Total de questes",
      value: questions.length, 
    },
    {
      label: "Questes corretas",
      value: rightAnswers,
    },
    {
      label: "Questes erradas",
      value: wrongAnswers,
    },
    {
      label: "Porcentagem",
      value: `${percentage}%`,
    },
  ]

  useEffect(() => {
    function handleKeyDown(e) {
      if(e.key === "Escape") {
        closeUI();
      };
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  return (
    <main 
      id="shootingTestResult"
      className="container"
    >
      <Button 
        icon={xIcon}
        type="closeBtn"
        onClick={closeUI}
      />

      <section>
        <h1>Resultado do teste</h1>

        <p>Segue abaixo o resultado do seu teste</p>

        <article>
          { resultFields.map( (field, index) => (
            <div
              key={index}
            >
              <p> {field.label} </p>
              <aside> {field.value} </aside>
            </div>
          ))}
        </article>

        <Button 
          label={resultFields[0].value == resultFields[1].value ? "Prxima etapa" : "Voltar para o incio"}
          onClick={() => {
            if(resultFields[0].value == resultFields[1].value ) {
              fetchNui('nextStep',[resultFields[0].value,resultFields[1].value])
            } else {
              navigate("/")
            }
          }}
        />
      </section>

    </main>
  )
};