import React from "react";
import { isEnvBrowser } from "../utils/misc.js";
import { BrowserRouter, HashRouter } from "react-router-dom";
import ProjectsApp from "../projects/index.jsx";

export default function RoutesManager() {
    const isBrowser = isEnvBrowser()

    const routes = (
        <>
            <ProjectsApp />
        </>

    )

    return (
        <>
            {isBrowser ? (
                <BrowserRouter>
                    {routes}
                </BrowserRouter>
            ) : (
                <HashRouter>
                    {routes}
                </HashRouter>
            )
            }
        </>
    )

}
