import { Route, Routes } from "react-router-dom";

import Home from "./Porte/pages/Home";
import Result from "./Porte/pages/Result";
import HotTrack from "./Porte/pages/HotTrack";
import Questions from "./Porte/pages/Questions";
import SeekGunLicense from "./Porte/pages/SeekGunLicense";

import ShootingTestContextProvider from "./Porte/context/ShootingTestContext";

import "./index.css";

export default function ShootingTest() {
  return (
    <div id="shootingTestWrapper">
      <ShootingTestContextProvider>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/result" element={<Result />} />
          <Route path="/questions" element={<Questions />} />

          <Route path="/hotTrack" element={<HotTrack />} />

          <Route path="/seekGunLicense" element={<SeekGunLicense />} />
        </Routes>
      </ShootingTestContextProvider>
    </div>
  )
};