const { isEnvBrowser } = require("./misc");

/**
 * Manda Nui callback para o backend
 * 
 * 
 * @param eventName -> Nome de um Evento, para passar informaes ao back-end
 * @param data -> Dados enviados para serem tratados pelo back-end;
 * @param mockData - dados que voc espera que a funtion retorne, Serve para simular uma resposta do back-end
 * 
 * 
 * Simple wrapper around fetch API tailored for CEF/NUI use. This abstraction
 * can be extended to include AbortController if needed or if the response isn't
 * JSON. Tailor it to your needs.
 * @param eventName - The endpoint eventname to target
 * @param data - Data sent by FiveM Side
 * @param mockData - Mock data to be returned if in the browser
 *
 * @return returnData - A promise for the data sent back by the NuiCallbacks CB argument
 */
async function fetchNui(eventName, data, mockData) {
    const options = {
        method: 'post',
        headers: {
            'Content-Type': 'application/json; charset=UTF-8',
        },
        body: JSON.stringify(data),
    };

    if (isEnvBrowser() && mockData) {
        return mockData;
    }

    try {
        const resourceName = window.GetParentResourceName ? window.GetParentResourceName() : 'nui-frame-app';

        const resp = await fetch(`https://${resourceName}/${eventName}`, options);

        const respFormatted = await resp.json();

        return respFormatted;
    } catch (error) {
        //console.log(`Error: Event ${eventName} didn't complete fetchNui: `, error)
    }
}

export { fetchNui }
