import { useContext, useEffect } from "react";
import { useNavigate } from "react-router-dom";

import { ShootingTestContext } from "../../context/ShootingTestContext";

import Button from "../../components/Button";

import xIcon from "../../assets/icons/x.svg";
import characterImg from "../../assets/character.png";
import attentionIcon from "../../assets/icons/attention.svg";

import "./styles.css";
import { fetchNui } from "../../../../utils/fetchNui";

export default function Home() {
  const { 
    closeUI,
    validity,
    testPrice,
    questions,
  } = useContext(ShootingTestContext);

  const navigate = useNavigate();

  useEffect(() => {
    function handleKeyDown(e) {
      if(e.key === "Escape") {
        closeUI();
      };
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  async function goToQuestions(){
    let query = await fetchNui('tryPayment')
    if(query){
      navigate("/questions")
    }
  }

  return (
    <main 
      id="shootingTestHome"
      className="container"
    >
      <img src={characterImg} />

      <Button 
        icon={xIcon}
        type="closeBtn"
        onClick={closeUI}
      />

      <section>
        <h1>Porte de arma</h1>

        <p className="welcome">
          Bem-vindo(a) ao nosso sistema de armas da cidade, onde voc poder comprar seus <strong>armamentos</strong> e equipa-los com ferramentas de tima qualidade <strong>ttica</strong>.
        </p>

        <article>
          { validity === null ? (
            <>
              <p>
                Voc ir responder um total de <strong>{questions.length} questes</strong>, sendo necessrio acertar todas delas para receber liberao para realizar uma prova prtica de manuseio pontaria do armamento!
              </p>

              <div className="testPrice">
                Valor do teste: <strong>R$ {testPrice.toLocaleString("pt-BR")} </strong>
              </div>
            </>
          ) : (
            <>
              <img src={attentionIcon} />

              <p>
                Vimos no nosso sistema de porte, que o senhor(a) j fez o teste de tiro recentemente na cidade, o senhor(a) s poder fazer outro depois que vencer o seu contrato.
              </p>

              <div className="testPrice">
                Validade: <strong>{ validity.toLocaleDateString("pt-BR") } </strong>
              </div>
            </>
          )}
        </article>

        { validity === null && (
          <Button 
            label="Iniciar prova"
            onClick={goToQuestions}
          />
        )}
      </section>
    </main>
  )
};